<template>
  <div class="wrap">
    <Banner :title="title" />
    <div class="container" v-show="isLoad">
      <div class="status pa3 tc">
        <div class="crSkyBlue rfont56 mt4 mb4" v-if="order.order_status == 1">...</div>
        <div class="crGirlPink rfont56 mt4 mb4" v-if="order.order_status == 0"></div>
        <div class="cr333 rfont56 mt4 mb4" v-if="order.order_status == 3"></div>
        <Price class="tc df" isRow="true" :payload="item" v-if="order.order_status != 0" />
        <div class="rejected font14 tc cr333" v-if="order.order_status == 0">()</div>
      </div>
      <div class="hr mb4"></div>
      <div class="pl4 pr4 df center">
        <span class="font16 cr333" @click="redirectGoodDetail"></span><i class="fa-icon fa-more"></i>
      </div>
      <div class="ml4 hr mt4"></div>
      <div class="pa4 mt1 df center item">
        <Good :payload="item" />
        <div class="ml4">
          <div class="font18 cr333">{{ order.order_name }}</div>
          <Operator class="mt3 font11" :payload="item" />
        </div>
      </div>
      <div class="meta font14 pl4 pr4 mt4">
        <p>{{ order.order_number }}</p>
        <p>{{ order.create_time | formatTime }}</p>
        <p>{{ order.tel | mosaicPhone }}</p>
        <p>{{ operator }}</p>
        <p>{{ order.order_name }}</p>
      </div>
      <div class="hr mt4"></div>
      <div class="font14 failed pl4 cr999" v-if="order.order_status == 0 && order.order_category == 18">
        <div class="bold"></div>
        <p>1.2G</p>
        <p></p>
        <p></p>
        <p class="seperator"></p>
        <p>2.</p>
        <p class="seperator"></p>
        <p>3.</p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
      </div>
      <div class="font14 failed pl4 cr999" v-if="order.order_status == 0 && order.order_category == 19">
        <div class="bold"></div>
        <p>1.</p>
        <p> </p>
        <p></p>
        <p class="seperator"></p>
        <p>2.1-21-5</p>
        <p>1-20</p>
      </div>
      <div class="tc font12 cr999 copyright"></div>
    </div>
    <div class="container" v-show="!isLoad">
      <div class="tc df cr666" style="margin-top: 3rem"></div>
    </div>
    
  </div>
</template>
<script scoped>
  import { mapGetters } from 'vuex';
  import { Func } from '../../vendor/lib';
  import Banner from '../../components/banner';
  import Good from '../../components/good';
  import Price from '../../components/price';
  import Operator from '../../components/operator';

  export default {
    data () {
      return {
        title: '', item: {}, raw: {}, operator: '', order: {}, isLoad: false,
        params: {
          size: 10,
        }
      }
    },
    components: {
      Banner, Good, Price, Operator
    },
    computed: {
      ...mapGetters({
        memory: 'getAllocateVariable',
      })
    },
    filters: {
      formatTime (time) {
        return Func.formatTime(time, 'YYYY-MM-DD H:mm');
      },
      trimName (str) {
        const chips = (str + '').match(/[0-9]+/);
        if (chips) {
          return chips[0];
        }
      },
      mosaicPhone (phone) {
        if (!!phone && phone.length) {
          return phone.replace(/(\d{3})\d{4}(\d{3})/, '$1****$2');
        }
        return phone;
      }
    },
    methods: {
      bubble (subscription) {
        this.$store.dispatch('bubbleDelegation', { subscription, page: this });
      },
      request (subscription, payload) {
        this.$store.dispatch('requestCustomer', { request: subscription, payload, page: this });
      },
      redirectGoodDetail () {
        this.$nextTick(() => {
          const params = { ...this.params, gid: this.item.id }
          this.request('redirect', {
            path: '/student/mall/goodsdetail', params
          })
        });
      }
    },
    mounted () {
      this.bubble('selectOrderDetail');
    },
    watch: {
      'memory.selectOrderDetail' (counter) {
        if (counter != 0) {
          this.isLoad = true;
        }
      }
    }
  }
</script>
<style scoped>
  .item {
    border-bottom: .12rem solid #f8f8f8;
  }
  .meta p {
    margin: .05rem 0;
    color: #666;
  }
  .status {
    height: 3rem;
  }
  .fa-more {
    width: .6rem;
    height: .6rem;
    background-size: .6rem .6rem;
  }
  .copyright {
    margin-top: 1.6rem;
    margin-bottom: 3.04rem;
  }
  .failed {
    margin-top: .44rem;
  }
  .failed div {
    margin-bottom: .2rem;
  }
  .failed p {
    margin-bottom: .08rem;
  }
  .failed .seperator {
    margin-bottom: .2rem;
  }
</style>



