// vue
import Vue from 'vue';
// vue-router
import Vuex from 'vuex';
// core
import core from './modules/core';
// helper
import helper from './modules/helper';
// vuevuex
Vue.use(Vuex);
// store
const store = new Vuex.Store({
    modules: {
        core, helper
    }
});
// 
const options = {
    appOptions: {
        store
    }
}
// 
export default options;


