// vue
import Vue from 'vue';
// vue-router
import VueRouter from 'vue-router';
// vuevue-router
Vue.use(VueRouter);
// 
import routes from './router';
// vue-router
const routerOptions = {
    mode: 'hash',
    base: __dirname,
    routes
}
// vue-router
const router = new VueRouter(routerOptions);
// 
const options = {
    appOptions: {
        router
    }
}
// router
export default options;


